import React, { useState, FormEvent, ChangeEvent, ReactElement } from 'react'
import {
    Button,
    Input,
    PasswordInput,
} from '@ya.praktikum/react-developer-burger-ui-components'
import cn from 'classnames'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import resetPasswordStyles from './reset-password.module.css'
import { useDispatch } from '../../../store'
import { passwordReset } from '../../../services/actions/auth-action/auth-thunk'

type TForm = {
    password: string
    token: string
}

export const ResetPassword = React.memo((): ReactElement => {
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const [form, setForm] = useState<TForm>({
        password: '',
        token: '',
    })

    const onChange = (e: ChangeEvent<HTMLInputElement>) => {
        setForm({ ...form, [e.target.name]: e.target.value })
    }

    const handleSubmit = (e: FormEvent) => {
        e.preventDefault()
        passwordReset({
            password: form.password,
            token: form.token,
        })(dispatch).then(() => {
            localStorage.removeItem('correctEmail')
            navigate('/login')
        })
    }

    if (!localStorage.getItem('correctEmail')) {
        return <Navigate to="/" />
    }

    return (
        <section className={resetPasswordStyles.wrapper}>
            <form
                onSubmit={handleSubmit}
                autoComplete="on"
                className={resetPasswordStyles.container}
            >
                <h1 className={cn(resetPasswordStyles.header, 'mb-6')}>
                     
                </h1>
                <PasswordInput
                    onChange={onChange}
                    value={form.password}
                    placeholder="  "
                    name="password"
                    extraClass="mb-6"
                />
                <Input
                    onChange={onChange}
                    placeholder="   "
                    type="text"
                    name="token"
                    value={form.token}
                    extraClass="mb-6"
                />
                <Button
                    htmlType="submit"
                    type="primary"
                    size="medium"
                    extraClass="mb-20"
                >
                    
                </Button>
                <h2 className={cn(resetPasswordStyles.someProblem, 'mb-4')}>
                     ?
                    <Link className={resetPasswordStyles.link} to="/login">
                        
                    </Link>
                </h2>
            </form>
        </section>
    )
})
